import { LoggerService } from '@nestjs/common';
import { HubLogger, IHubLoggerOptions, LogLevel } from 'hub-logger';

class HubLoggerService implements LoggerService {
  hubLogger: HubLogger

  constructor(options?: IHubLoggerOptions) {
    this.hubLogger = new HubLogger(options);
  }

  log(message: string) {
    this.hubLogger.info(message);
  }

  error(message: string, trace: string) {
    this.hubLogger.error(message, {
      trace
    });
  }

  warn(message: string) {
    this.hubLogger.warn(message);
  }

  debug(message: string) {
    this.hubLogger.debug(message);
  }

  verbose(message: string) {
    // reduce the number of actual log levels
    this.debug(message);
  }
}

export { HubLoggerService, IHubLoggerOptions, LogLevel }